import React from "react";
import {
  AppBar,
  Toolbar,
  IconButton,
  Drawer,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Tooltip,
} from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import {
  Home as HomeIcon,
  Work as WorkIcon,
  School as SchoolIcon,
  Description as DescriptionIcon,
  Apps as AppsIcon,
  ContactSupport as ContactSupportIcon,
} from "@mui/icons-material";
import Logo from "./logo";

const Header: React.FC = () => {
  const [drawerOpen, setDrawerOpen] = React.useState(false);

  const toggleDrawer =
    (open: boolean) => (event: React.KeyboardEvent | React.MouseEvent) => {
      if (
        event.type === "keydown" &&
        ((event as React.KeyboardEvent).key === "Tab" ||
          (event as React.KeyboardEvent).key === "Shift")
      ) {
        return;
      }
      setDrawerOpen(open);
    };

  const menuItems = [
    { text: "Home", icon: <HomeIcon className="text-slate-600" /> },
    { text: "Experience", icon: <WorkIcon className="text-slate-600" /> },
    { text: "Education", icon: <SchoolIcon className="text-slate-600" /> },
    { text: "Resume", icon: <DescriptionIcon className="text-slate-600" /> },
    { text: "Portfolio", icon: <AppsIcon className="text-slate-600" /> },
    { text: "Blog", icon: <ContactSupportIcon className="text-slate-600" /> },
    { text: "Footer", icon: <ContactSupportIcon className="text-slate-600" /> },
  ];

  return (
    <AppBar
      className="header-default black-logo-version shadow-lg bg-white"
      position="sticky"
      style={{ backgroundColor: "white" }}
    >
      <Toolbar className="flex justify-between items-center lg:mx-20">
        <Logo />
        <div className="flex items-center">
          <nav className="mainmenu-nav navbar-example2 hidden lg:block">
            <ul className="primary-menu flex flex-col space-y-2 lg:flex-row lg:space-x-2 lg:space-y-0">
              {menuItems.map((item, index) => (
                <li className="nav-item" key={index}>
                  <Tooltip title={item.text}>
                    <IconButton color="inherit">{item.icon}</IconButton>
                  </Tooltip>
                </li>
              ))}
            </ul>
          </nav>
        </div>
        <div className="block lg:hidden">
          <Tooltip title="menu">
            <IconButton
              className="hamberger-menu"
              color="inherit"
              aria-label="menu"
              onClick={toggleDrawer(true)}
            >
              <MenuIcon className="text-slate-600" />
            </IconButton>
          </Tooltip>
          <Drawer
            anchor="right"
            open={drawerOpen}
            onClose={toggleDrawer(false)}
            className="drawer-menu rounded-lg"
          >
            <div className="p-4">
              <List>
                {menuItems.map((item, index) => (
                  <ListItem button key={index}>
                    <ListItemIcon>{item.icon}</ListItemIcon>
                    <ListItemText primary={item.text} />
                  </ListItem>
                ))}
              </List>
            </div>
          </Drawer>
        </div>
      </Toolbar>
    </AppBar>
  );
};

export default Header;
